
.row{
	display: flex;
	flex-wrap: wrap;
	width: 90%;
	margin: auto;
	padding-top: 5%;	
	padding-bottom: 3%;
}
.ul{
	list-style: none;
}
.footer{
	/* background-color: #24262b; */
	background-image: url(../../assect/kid1.jpg);
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
    /* padding: 10px 10px; */
	width: 100%;
	/* background-image:  rgba(0,0,0,0.5); */
	
}
.footer-col{
   width: 25%;
   padding: 0 15px;
}
.footer-col h4{
	font-size: 18px;
	color: #fff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: #fabd3a;
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer-col ul li:not(:last-child){
	margin-bottom: 10px;
}
.footer-col ul li a{
	font-size: 16px;
	text-transform: capitalize;
	color: #fff;
	text-decoration: none;
	font-weight: 300;
	color: #fff;
	display: block;
	transition: all 0.3s ease;
}
.footer-col ul li a:hover{
	color: #fff;
	padding-left: 8px;
}
.footer-col .social-links a{
	display: inline-block;
	height: 40px;
	width: 40px;
	background-color: rgba(255,255,255,0.2);
	margin:0 10px 10px 0;
	text-align: center;
	line-height: 40px;
	border-radius: 50%;
	color: #fff;
	transition: all 0.5s ease;
}
.footer-col .social-links a:hover{
	color: #000;
	background-color: #fff;
}
.div-opacity{
	opacity: 0.7;
}
.div-color{
	background-color : #000;
}

/*responsive*/
@media(max-width: 767px){
  .footer-col{
    width: 50%;
    margin-bottom: 30px;
}
}
@media(max-width: 574px){
  .footer-col{
    width: 100%;
}
}